<template>
  <div>
    <wk-nav :stus="stus" :activeIndex="activeIndex" @check="checkItem"></wk-nav>
    <wk-content @correct="correct" :page-disabled="pageDisabled" :current-stu="currentStu" @check="checkItem" :active-index="activeIndex"></wk-content>
  </div>
</template>

<script>
  import wkNav from './wk-nav.vue'
  import wkContent from './wk-content.vue'
  export default {
    created() {
      this.changeDisabled()
    },
    props: {
      stus: {
        type: Array
      },
      currentStu: {
        type: Object
      }
    },
    computed: {
//      currentStu() {
//        return this.stus[this.activeIndex]
//      }
    },
    methods: {
      correct(data) {
        this.$emit('correct', data)
      },
      checkItem(index) {
        this.$emit('checkItem', index)
        this.activeIndex = index
        this.changeDisabled()
      },
      changeDisabled() {
        this.pageDisabled.pre = this.activeIndex === 0
        this.pageDisabled.next = this.activeIndex === this.stus.length - 1
      }
    },
    components: {
      wkNav,
      wkContent
    },
    data() {
      return {
        pageDisabled: {pre: false, next: false},
        activeIndex: 0,
        dialogVisible: false
      }
    }
  }
</script>

<style scoped>
  .my-aside {
    height: 100%;
    float: right;
    margin: 0;
    width: auto;
    color: #696e79;
  }
  .my-aside .el-dialog__close {
    color: #fa7252;
  }
  .my-aside .el-dialog__close:hover {
    color: #fa7252;
  }
  .my-aside .el-dialog__body {
    height: 100%;
    padding: 0;
  }
  .my-aside .el-dialog__header {
    padding: 0;
  }
</style>



