<template>
    <div>
        <!--<button @click="showAside">show aside</button>-->
        <!--<button @click="dialog">show dialog</button>-->
        <el-button @click="showAside">show dialog</el-button>
        <el-dialog :visible.sync="dialogVisible" custom-class="my-aside" top="0">
            <wk-mgr :stus="stus" @check-item="checkItem" @correct="correct" :current-stu="currentStu" class="h-full"></wk-mgr>
        </el-dialog>
        <el-dialog :visible.sync="dialog2Visible" customClass="my-aside" top="0">
            <comment-mgr :stus="stus" @check-item="checkItem" @correct="correct" :current-stu="currentStu" class="h-full"></comment-mgr>
        </el-dialog>
        <button @click="notify">show notify</button>
        <el-row class="vertical-center">
            <el-col :span="10" :offset="5">
                <el-form class="login-from" :model="form" ref="form" label-width="80px" label-position="left" :inline-message="true">
                    <el-form-item :label="$t('username')" prop="username" :required="true">
                        <el-input v-model="form.username" placeholder="admin"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('password')" prop="password">
                        <el-input v-model="form.password" type="password" placeholder="admin"></el-input>
                    </el-form-item>
                    <el-form-item label-width="0">
                        <el-button class="btn-login" type="primary" :disabled="invalid" native-type="submit" @click.prevent="submit" v-text="$t('login')"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script type="text/javascript">
    import wkMgr from '../wkMgr/wk-mgr.vue'
    import commentMgr from '../wkMgr/wk-comment-mgr'

    export default {
        components: {
            wkMgr,
            commentMgr
        },
        data() {
            return {
                form: {},
                dialogVisible: false,
                dialog2Visible: false,
                invalid: false,
                stus: [],
                currentStu: {user: {name: ''}, homework: {content: ''}}
            }
        },
        rules: {

        },
        methods: {
            correct(data) {
                console.info(data)
            },
            submit() {
                this.$http.get('webapp/auth_error').then(console.info)
            },
            checkItem(index) {
                this.queryCurrentStu(this.stus[index].id)
            },
            queryCurrentStu(id) {
                this.$http.get('homework/homeworks/submitted_works/' + id)
                .then(resp => resp.data)
                .then(resp => {
                    this.currentStu = resp
                })
            },
            dialog() {

            },
            notify() {
                this.$notify({
                    type: 'error',
                    title: 'hi',
                    message: 'mmmmm'
                })
                this.dialog2Visible = true
            },
            showAside() {
                this.$http.get('homework/homeworks/1/submitted_works')
                .then(resp => resp.data)
                .then(resp => {
                    this.stus = resp
                    if (resp && resp.length) {
                        this.queryCurrentStu(resp[0].id)
                    }
                })
                this.dialogVisible = true
            }
        }
    }
</script>

<style>
    .my-aside {
        height: 100%;
        float: right;
        margin: 0;
        width: auto;
        color: #696e79;
    }
    .my-aside .el-dialog__close {
        color: #fa7252;
    }
    .my-aside .el-dialog__close:hover {
        color: #fa7252;
    }
    .my-aside .el-dialog__body {
        height: 100%;
        padding: 0;
    }
    .my-aside .el-dialog__header {
        padding: 0;
        height: 0;
    }
</style>



