<template>
  <div class="wk-nav">
    <div class="wk-nav-title">学生列表</div>
    <div class="wk-nav-content">
      <ul>
        <li :class="i === activeIndex ? 'active' : ''" :key="i" v-for="(stu, i) in stus" @click="$emit('check', i)">
          <span>{{stu.user.name}}</span>
          <span v-if="stu.starred === 'y'" class="crrected">(已批改)</span>
          <span v-else class="uncrrect">(未批改)</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      stus: {
        type: Array,
        default: () => []
      },
      activeIndex: {
        type: Number,
        default: 0
      }
    }
  }
</script>

<style>
  .wk-nav + div {
    margin-left: 300px;
  }
</style>

<style scoped>
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  li {
    height: 50px;
    line-height: 50px;
    cursor: pointer;
    position: relative;
  }
  li span:first-child {
    padding: 0 20px;
    line-height: 50px;
  }
  li:nth-child(odd) {
    background-color: #f5f7f8;
  }
  li:hover, li.active {
    background-color: #fff;
    color: #0c92fe;
  }
  li:hover .uncrrect, li.active .uncrrect {
    color: #ec670b
  }
  li:hover .crrected, li.active .crrected {
    color: #0fcb68;
  }
  .uncrrect {
    color: #ec670b;
  }
  .crrected {
    color: #0fcb68;
  }
  li.active:before {
    content: '';
    position: absolute;
    height: 50px;
    display: inline-block;
    width: 4px;
    background-color: #0c92fe;
  }
  .wk-nav {
    float: left;
    width: 300px;
    height: 100%;
    background-color: #fafbfb;
    padding-top: 50px;
    box-sizing: border-box;
  }
  .wk-nav-content {
    /*height: calc(100% - 50px);*/
    height: 100%;
    width: 100%;
    overflow-y: auto;
  }
  .wk-nav-title {
    margin-top: -50px;
    padding: 0 20px;
    line-height: 50px;
    background-color: #eaeff1;
    border-bottom: 1px solid rgba(105, 110, 121, 0.3);
    box-sizing: border-box;
    height: 50px;
  }

</style>



