<template>
    <div :class="['wk-content', isComment ? 'comment-main':'']">
        <div class="wk-content-title" v-if="isComment"><span class="text-lg">作业展示</span></div>
        <div class="wk-content-title" v-else><span class="text-lg">作业：</span>建模基础方法</div>
        <div class="wk-content-main">
            <div class="warpper">
                <el-row :gutter="20" class="item">
                    <el-col :span="isComment? 21:20"><span>{{currentStu.homework.content}}</span></el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="isComment? 3:4"><label>答题内容：</label></el-col>
                    <el-col :span="isComment? 21:20"><textarea v-model="currentStu.answer" readonly></textarea></el-col>
                </el-row>
                <el-row :gutter="20" class="item">
                    <el-col :span="isComment? 3:4"><label>附件：</label></el-col>
                    <el-col :span="isComment? 21:20"><div class="file-btn">课程表模型-{{currentStu.name}}.pdm<i class="el-icon-download text-blod"></i></div></el-col>
                </el-row>
            </div>

            <div class="warpper-comment" v-if="isComment">
                <el-row :gutter="20" class="item comment-content">
                    <el-col :span="3"><label>赞：</label></el-col>
                    <el-col :span="21">adfadfa</el-col>
                </el-row>
                <el-row :gutter="20" class="item comment-content">
                    <el-col :span="3"><label>评论：</label></el-col>
                    <el-col :span="21" class="flex-box">
                        <textarea></textarea>
                        <el-button class="btn-gradient-lg m-l-sm">发表</el-button>
                    </el-col>
                </el-row>
            </div>
            <comment-content v-if="isComment"></comment-content>
        </div>
        <div class="wk-correct">
            <div class="wk-correct-main">
                <form class="warpper">
                    <img src="../../assets/profile.png" class="profile">
                    <p>
                        <span class="stu-name">{{currentStu.user.name}}</span>
                        <span class="corrected" v-if="currentStu.starred === 'y'">已批改</span>
                        <span class="uncorrect" v-else>未批改</span>
                    </p>
                    <hr/>
                    <p v-if="submitTime">
                        <span class="">提交时间:</span>
                        <span style="white-space: pre">{{submitTime}}</span>
                    </p>
                    <p v-if="scoreTime">
                        <span class="">批改时间:</span>
                        <span style="white-space: pre">{{scoreTime}}</span>
                    </p>
                    <hr/>
                    <div class="warpper text-left">
                        <span>作业等级</span>
                        <el-rate class="pull-right" v-model="score" :score-template="scoreTemplate" showScore allowHalf></el-rate>
                    </div>
                    <hr/>
                    <textarea placeholder="请填写作业评语" style="margin: 10px 0 60px; width: 100%; height: 140px; resize: none" v-model="comment"></textarea>
                    <p>
                        <el-button class="btn btn-gradient" @click="$emit('correct', {comment, score})">提交批改</el-button>
                    </p>
                    <p>
                        <el-button class="btn">取消展示</el-button>
                    </p>
                </form>
            </div>
            <div class="wk-correct-footer warpper">
                <el-button icon="el-icon-arrow-left" :disabled="pageDisabled.pre" @click="$emit('check', activeIndex - 1)">上一条</el-button>
                <el-button class="pull-right" :disabled="pageDisabled.next" @click="$emit('check', activeIndex + 1)">下一条<i class="el-icon-arrow-right el-icon--right"></i></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import { dateFormat } from '@/assets/js/untils'
    import commentContent from './wk-comment-content'
    const timeFormat = time => {
        if (time) {
            return dateFormat(time, 'yyyy/MM/dd       HH:mm:ss')
        }
        return time
    }
    export default {
        props: {
            pageDisabled: {
                type: Object
            },
            activeIndex: {
                type: Number
            },
            currentStu: {
                type: Object,
                default: () => ({homework: {content: ''}})
            },
            isComment: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                score: 0,
                comment: ''
            }
        },
        watch: {
            currentStu(newV) {
                this.score = newV.score * 0.5
                this.comment = newV.comment
            }
        },
        computed: {
            submitTime() {
                return timeFormat(this.currentStu.submitTime)
            },
            scoreTime() {
                return timeFormat(this.currentStu.scoreTime)
            },
            scoreTemplate() {
                return this.score * 2 + ''
            }
        },
        components: {
            commentContent
        }
    }
</script>

<style scoped>
    .wk-content {
        padding-top: 50px;
        box-sizing: border-box;
        width: 1249px;
        height: 100%;
        border-left: 1px solid rgba(105, 110, 121, 0.3);
    }
    .wk-content-title {
        margin-top: -50px;
        padding: 0 30px;
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #cad8df;
        box-sizing: border-box;
    }
    .wk-correct {
        overflow: hidden;
        background-color: #f5f7f8;
        height: 100%;
        box-sizing: border-box;
        padding-bottom: 100px;
    }
    .wk-correct-main {
        width: 100%;
        height: 100%;
        margin-bottom: -100px;
        text-align: center;
    }
    .wk-correct-footer {
        width: 100%;
        height: 100px;
        margin-top: 100px;
        box-sizing: border-box;
        border-top: 1px solid #cad8df;
    }
    .wk-content-main {
        float: left;
        width: 805px;
        height: 100%;
        overflow: auto;
    }
    .wk-content.comment-main {
        overflow: auto;
        width: 1550px;
    }

    .comment-main .wk-content-main {
        width: 1105px;
    }

    .wk-content-main textarea {
        width: 580px;
        height: 270px;
        box-sizing: border-box;
        resize: none;
    }

    .comment-main .wk-content-main textarea {
        width: 880px;
        height: 270px;
    }

    .wk-content-main .warpper-comment textarea {
        width: 600px;
        height: 100px;
        background-color: #ffffff;
        box-sizing: border-box;
    }

    .wk-content-main >.warpper {
        box-sizing: border-box;
    }

    .wk-content-main >.warpper-comment {
        padding: 0px 60px 10px 35px;
    }

    .warpper-comment > .comment-content:first-child {
        border-top: #cad8df 1px solid;
        padding-top: 30px;
    }

    .item > div:first-child {
        text-align: right;
    }

    .wk-content-main .item {
        margin: 20px 0;
        overflow: hidden;
    }

    .wk-correct-main >.warpper {
        height: 100%;
        box-sizing: border-box;
        overflow: auto;
    }

    .warpper {
        padding: 20px;
    }

    .profile {
        width: 85px;
        height: 85px;
        border-radius: 85px;
        overflow: hidden;
    }

    .stu-name {
        color: #0c92fe;
    }

    .uncorrect {
        color: #fff;
        background-color: #ec670b;
    }

    .corrected {
        color: #fff;
        background-color: #0fcb68;
    }

    .btn {
        width: 195px;
        height: 40px;
    }
    .btn-gradient-lg {
        width: 120px;
        height: 100px;
        background-image: linear-gradient(-90deg, #0090ff 0%, #6c50fd 100%);
        border-radius: 2px;
        padding: 0;
        color: white;
    }
    .file-btn {
        display: inline-block;
        color: #22a5df;
        width: 190px;
        height: 40px;
        line-height: 40px;
        background-color: rgba(69, 154, 222, 0.08);
        border-radius: 1px;
        padding: 0 20px;
        cursor: pointer;
    }

    .file-btn i {
        float: right;
        line-height: 40px;
    }

    .text-blod {
        font-weight: bold;
    }
    textarea[readonly] {
        background-color: #f5f7f8;
    }
</style>



