<template>
    <div>
        <wk-content @correct="correct" :page-disabled="pageDisabled" :is-comment="true" :current-stu="currentStu" @check="checkItem" :active-index="activeIndex"></wk-content>
    </div>
</template>
<script type="text/javascript">
    import wkContent from './wk-content.vue'
    export default {
        data () {
            return {
                pageDisabled: {pre: false, next: false},
                activeIndex: 0,
                dialogVisible: false
            }
        },
        methods: {
            correct(data) {
                this.$emit('correct', data)
            },
            checkItem(index) {
                this.$emit('checkItem', index)
                this.activeIndex = index
                this.changeDisabled()
            },
            changeDisabled() {
                this.pageDisabled.pre = this.activeIndex === 0
                this.pageDisabled.next = this.activeIndex === this.stus.length - 1
            }
        },
        props: {
            stus: {
                type: Array
            },
            currentStu: {
                type: Object
            }
        },
        components: {
            wkContent
        }
    }
</script>
<i18n>
    {
        "zh": {

        }
    }
</i18n>
<style scoped>
</style>



